import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { ApiResult } from 'src/app/core/models/common/ApiResult';
import { PublisherResponseVM } from 'src/app/core/models/customer/publisher/publisherResponse.ViewModel';
import { CustomerUrlPublisher } from 'src/app/core/utils/URL/customer-url/customer-publisher/customer-url-publisher';

@Injectable({
  providedIn: 'root'
})
export class PublisherService {

  constructor(private http: HttpClient, private customerUrlPublisher: CustomerUrlPublisher) {}
  getPublisher(publisherId: number): Observable<ApiResult<PublisherResponseVM>> {
		return this.http.get<ApiResult<PublisherResponseVM>>(this.customerUrlPublisher.getPublisher(publisherId));
	}
}
