import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { takeUntil } from 'rxjs/internal/operators/takeUntil';
import { AppErrorHandler } from 'src/app/core/error-handler/app-error-handler.service';
import { PublisherResponseVM } from 'src/app/core/models/customer/publisher/publisherResponse.ViewModel';
import { NotificationService } from 'src/app/core/notification/notifications/customer/notification.service';
import { DestroyService } from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import { PublisherService } from 'src/app/core/services/customer/publisher/publisher.service';
import * as ClassicEditor from '@ckeditor/ckeditor5-angular';

@Component({
  selector: 'app-publisher',
  templateUrl: './publisher.component.html',
  styleUrls: ['./publisher.component.less']
})
export class PublisherComponent implements OnInit {
	public Editor = ClassicEditor;
  isLoading = true; //skelton
	isNotHaveData: boolean = false;
	publisherId: number = 1;
	publisherResponseVM:PublisherResponseVM = {} as PublisherResponseVM;
	constructor(
		private publisherService: PublisherService,
		private route: ActivatedRoute,
		private notificationService: NotificationService,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler
	) {}

	ngOnInit(): void {
		this.isLoading = true;
		if (this.route.snapshot.paramMap.get('publisherId')) {
			this.publisherId = +this.route.snapshot.paramMap.get('publisherId');
			console.log(this.publisherId);
			this.getPublisher(this.publisherId);
		}
	}
	getPublisher(authorId: number) {
		this.publisherService
			.getPublisher(authorId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);
					if (result.isSuccessed) {
						if (result.resultObj) {
							console.log(result);
							this.publisherResponseVM = result.resultObj;
							this.isLoading = false;
						}
					} else {
						this.isNotHaveData = true;
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				},
				(error) => {
					this.isNotHaveData = true;
					this.appErrorHandler.handleErrorWithNoti(error);
				},
				() => {}
			);
	}

}
